* {
  box-sizing: border-box;
}

body {
  color: #333;
  background: #d0e1f5;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

body.dark-mode {
  fill: #fff;
  background-color: #333;
}

.dark-mode .card {
  background-color: #404047;
  box-shadow: 0 0 10px rgba(207, 207, 212, .5);
}

.wrapper {
  max-width: 1400px;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.page-title {
  text-align: center;
  color: #000;
  margin-top: 50px;
  margin-bottom: 100px;
  font-size: 46px;
}

.dark-mode .page-title {
  color: #fff;
}

.card {
  width: 28%;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 50px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(35, 35, 135, .5);
}

.card .title {
  color: #222;
  text-align: center;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 32px;
}

.dark-mode .card .title {
  color: #fff;
}

.card .text {
  color: #222;
  margin: 0;
  font-size: 18px;
  line-height: 1.5;
}

.dark-mode .card .text {
  color: #fff;
}

.toggle-mode {
  cursor: pointer;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: fixed;
  top: 30px;
  left: 30px;
}

.toggle-mode:hover {
  fill: #f06c2f;
  scale: 1.3;
}

.upward {
  width: 70px;
  height: 70px;
  color: #d2691e;
  cursor: pointer;
  text-align: center;
  opacity: 0;
  z-index: 999;
  background-color: #e3e612;
  border: 2px solid #ff5e00;
  border-radius: 50%;
  font-size: 20px;
  font-weight: 800;
  line-height: 70px;
  transition: all .5s;
  position: fixed;
  bottom: 700px;
  right: 40px;
}

.active {
  visibility: visible;
  opacity: 1;
  z-index: 999;
  bottom: 40px;
}

.upward:hover {
  color: #161688;
  background-color: #0ff;
  border: 2px solid #080884;
}

